export default {
  name: 'addUserForm',
  data () {
    return {
      item: {
        name: '',
        telephone: '',
        level: Number
      },
      level: [
        {
          value: 5,
          label: 'PI'
        },
        {
          value: 4,
          label: 'assistant'
        },
        {
          value: 3,
          label: 'PR'
        },
        {
          value: 2,
          label: 'student'
        },
        {
          value: 1,
          label: 'stranger'
        }
      ]
    }
  },
  methods: {
    deleteMe () {
      // this.$axios.delete(this.url + '/user/id/' + localStorage.getItem('id'))
      this.$axios.delete(this.url + '/user/id/7')
    },
    submit () {
      if (this.item.name === '' || this.item.telephone === '') {
        this.$notify.warning({
          title: '请输入姓名和电话!'
        })
        return
      }
      if (this.item.level === 0) {
        this.$notify.warning({
          title: '请选择用户身份!'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/user/',
        data: JSON.stringify(this.item)
      }).then((user) => {
        if (user.status === 200) {
          alert('添加成功')
          // this.item = []
          // this.item.level = 0
          // const data = user.data
        } else {
          alert(user)
        }
      }).catch((err) => {
        alert(err)
      })
    }
  },
  created () {
    this.item.level = 0
  }
}



